import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";

Vue.use(VueRouter);

const routes = [{
        path: "/home",
        name: "Home",
        component: Home,
    },
    {
        path: "/",
        name: "Index",
        component: () =>
            import ("../views/index.vue"),
    },
    {
        path: "/setting",
        name: "Setting",
        component: () =>
            import ("../views/setting.vue"),
    },
];

const router = new VueRouter({
    routes,
});

export default router;